body{
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  background-color: black;
}

.main_css{
  position: relative;
  width: 100%;
  overflow: hidden;
}

.hidden{
  display: none !important;
}

.no-interaction {
  pointer-events: none;
}

.news-paper{
  position: absolute;
  object-fit: cover;
  width: 100%;
  top: 10%;
  z-index: 2;
}

